* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: black;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  color: white;
}

.title-header {
  display: flex;
  align-items: center;
}

.title-name {
  margin-right: 20px;
  font-family: gilroy;
}

.title-name h1 {
  font-size: 36px;
  font-weight: bold;
  font-style: italic;
}

.products {
  display: flex;
  align-items: center;
  font-size: 18px;
}

.products h3 {
  margin-right: 10px;
}

.products h3 a {
  color: white;
  text-decoration: none;
}

.products h3 a:hover {
  text-decoration: none;
  font-size: 18px;
}

.login {
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 15px;
  background-color: white;
  color: black;
  border: none;
}

/* Responsive Styles */
@media screen and (max-width: 600px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .title-header {
    margin-bottom: 10px;
  }

  .products {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
  }

  .login {
    margin-top: 10px;
  }
}
